	module.exports = {"title":"フェルメールの日常・レンブラントの非日常","date":"2016-01-15 07:29:19 (金)","layout":"post","path":"/20160115/","body":"<h2>フェルメールの日常・レンブラントの非日常 - 三浦佳世 | 図書803号</h2>\n<blockquote>\n<p>視覚研究者のジェニファー・サンとピエトロ・ペローナは、欧米の著名な美術館に収蔵されている多くの絵画の中の光源の位置を調べた。すると、八割近くの絵が左からの光、それも左上30~60度からの光を想定して描かれていることがわかった。</p>\n</blockquote>\n<blockquote>\n<p>人は無意識のうちに、光源を仮定し、陰影の状態を確認し、両者の関係を判断して、凹凸を推測しているらしい。驚くことに、こうした高度で瞬時の無意識的推論は生後6、7ヶ月にもなればできるという。</p>\n</blockquote>\n<blockquote>\n<p>複数の凹凸図形の中から「仲間はずれ」を探し出す課題（視覚探索）で、　〜中略〜　右利きの参加者では、左上30~60度からの光に照らし出される場合に、もっとも早く「仲間はずれ」を探し出せたという。つまり真上からの光よりも左上からの光に鋭敏だったのである\n。</p>\n</blockquote>\n<blockquote>\n<p>右から射す光は多く描かれない。それだからこそ、何か特別なことが起こったという印象を喚起する。</p>\n</blockquote>\n<h2>バルミューダ</h2>\n<p><a href=\"http://irritantis.info/2016/01/learn-from-balmuda-the-toaster/\">きみはバルミューダほどシズるWebを設計できるか</a></p>\n<h2>これからは、LINEで宅急便！</h2>\n<p><a href=\"http://www.kuronekoyamato.co.jp/campaign/renkei/LINE/index.html\">http://www.kuronekoyamato.co.jp/campaign/renkei/LINE/index.html</a></p>\n<p>2016.1.19からLINEで宅急便の到着予定や不在通知が送信される。さらに受け取り日時の変更や受け取り場所も変更できる。やばい。</p>\n"}

