	module.exports = {"title":"うんちのゆくえ","date":"2016-01-26 07:40:59 (火)","layout":"post","path":"/20160126/","body":"<blockquote class=\"twitter-tweet\" lang=\"ja\"><p lang=\"ja\" dir=\"ltr\">ウェブは「読む」のではなく、「ざっと見る」ものであることが数多くのユーザビリティの調査結果から証明されている。ウェブの文章を書くのなら、この事実を認識しておくべきである。</p>&mdash; ヤコブ・ニールセン (@jakob_usability) <a href=\"https://twitter.com/jakob_usability/status/691762918115917825\">2016, 1月 25</a></blockquote>\n<h2>ギットクエスト</h2>\n<p><a href=\"http://unit8.net/gq/\">http://unit8.net/gq/</a></p>\n<h2>「スケールしないことをしよう」</h2>\n<p><a href=\"http://postd.cc/do-things-that-dont-scale/\">ポール・グレアムによる「スケールしないことをしよう」前編</a></p>\n<h2>うんちのゆくえ</h2>\n<p><a href=\"http://www.waterworks.co.jp/vol14/page1.html\">ウンチのゆくえ</a></p>\n<blockquote>\n<p>意外に思うかもしれないが、ウンチの成分の半分以上、それどころか75％が、なんと水である。</p>\n</blockquote>\n<blockquote>\n<p>東京都の人口は、2009年6月時点で、1297万6698人である。計算しやすいよう、1300万人とする。そして日本人1日のウンチの排泄量平均が、150～200ｇ 。少なく見積もって150ｇで計算してみよう。1300万人×150ｇ＝1950ｔになる。</p>\n</blockquote>\n<blockquote>\n<p>雨水までも、ウンチを含んだ汚水とともに一緒になって下水を流れているとは、多くの人が知らないのではないか。汚水と雨水を一緒に流す下水道方式を合流式という。別々に流す方式を分流式という。</p>\n</blockquote>\n<blockquote>\n<p>ウンチを含んだ汚水は、大部分は沈殿と微生物という自然の力によって浄化されていたのである。</p>\n</blockquote>\n<blockquote>\n<p>その程度の口径で、下水管が詰まらないかですって？　詰まらないです。生活排水に含まれる固形物の濃度は、1リットルあたりわずか0.2ｇですから。ほとんど水なんですよ</p>\n</blockquote>\n<blockquote>\n<p>東京を含めた都市部の下水には悩みがありましてね。早い時期に下水道を整備した都市は、雨水も生活排水も一緒に流される合流式（P2参照）。そのため大雨で下水量が晴天時の3倍以上になると、下水処理能力を超えてしまうので、そのときは直接放流といってやむなく河川に汚水をそのまま流しているのが現状なのです</p>\n</blockquote>\n"}

