	module.exports = {"title":"顧客価値戦略サミット2015","date":"2016-01-28 06:53:30 (木)","layout":"post","path":"/20160128/","body":"<h2>「顧客価値戦略サミット2015」レポート 【第3部：ソニー損保様トークセッション】</h2>\n<p><a href=\"http://www.bebit.co.jp/info/column/customer-value-strategy-summit-2015-03.html\">http://www.bebit.co.jp/info/column/customer-value-strategy-summit-2015-03.html</a></p>\n<blockquote>\n<p>「また自動車保険に契約しているお客様のうち、事故を経験されるのは全体の1割ほど。それ以外のお客様とは契約期間中ほとんど接点がありません。広域災害のような緊急時こそ、お客様から信頼していただけるようなコミュニケーションを取れる良いチャンスとして捉えています。」</p>\n</blockquote>\n<blockquote>\n<p>NPSを収益と紐づけることで、経営陣の関心も高まった</p>\n</blockquote>\n<blockquote>\n<p>「その上でNPSを1上げることによって、継続率がどのくらい上がるのか、新規顧客がどの程度増えるのか、その結果としてどのくらい財務的なインパクトがあるのか。これらをデータとして示したことで、経営陣が当社成長におけるNPS向上の重要性を再認識し、その後の活動がスムーズに進むようになりましたね」</p>\n</blockquote>\n<blockquote>\n<p>「施策によってはコストが増えるという面は確かにあります。ただ、実はカスタマーエクスペリエンス向上に重要な要素の一つは、『対応のスムーズさ』です。つまり、カスタマーエクスペリエンスを向上させることはお客様だけでなく、現場のオペレーターの応対時間を減らし、業務負荷やコストの削減にもつながることもあります。なので、必ずしもトレードオフではないんです。」</p>\n</blockquote>\n<blockquote>\n<p>現場にやらされ感が出たら、うまくいかない。CXデザイン部はあくまでサポート役</p>\n</blockquote>\n<p>カスタマーエクスペリエンス向上のために大小2つのループ</p>\n<blockquote>\n<p>「1つ目の大きなループは部門長レベルが参加する「NPS向上会議」、2つ目の小さなループは現場を巻き込んだ「VOG（Voice of Genba（現場））会議」</p>\n</blockquote>\n<blockquote>\n<p>NPS向上会議は、部門横断で改善すべきテーマを明確にし、現場側で改善を行うための工夫や裁量の余地を生み出すことが目的です。</p>\n</blockquote>\n<blockquote>\n<p>そのテーマについて現場メンバーと一緒になってカスタマージャーニーを書いたり、施策を考えたりしていくのがVOG会議です</p>\n</blockquote>\n<p>CJM活用のコツ</p>\n<blockquote>\n<p>発見された課題の構造化がキモだと思います。カスタマージャーニーを書くことで100個の課題が出てきたとしても、それを全部やるわけにはいかない。それら100個の課題の背後にある3つぐらいの根本課題を見つけるファシリテーションが重要です。</p>\n</blockquote>\n<p>組織に顧客ロイヤルティ追求の思想を広めていく</p>\n<blockquote>\n<p>NPSのアンケート調査で低評価の回答をされたお客様には原則48時間以内にお電話し、お詫びと改善すべき点をお伺いすることも始めました</p>\n</blockquote>\n<blockquote>\n<p>マーケティング部門のメンバーは、コールセンターの通話録音を聞く「聞きおこし100本ノック」をしばしば以前からやっています</p>\n</blockquote>\n<h2>言語処理100本ノック 2015</h2>\n<p><a href=\"http://www.cl.ecei.tohoku.ac.jp/nlp100/#ch1\">http://www.cl.ecei.tohoku.ac.jp/nlp100/#ch1</a></p>\n"}

