	module.exports = {"title":"江ノ島・長谷散歩","date":"2016-02-06 08:39:00 (土)","layout":"post","path":"/20160206/","body":"<p>6時に起き、ウインナーと小松菜を炒め、おにぎりを握る。8時前に家を出て駅に向かう。横浜でJRに乗り換えて藤沢へ、そこから小田急で片瀬江ノ島へ。<br>\n社会人になってからは年に一度江ノ島の弁財天に参拝している。特に信心が強いわけではないが、昨年のお札の返却とお礼をし、一年のご加護を祈るようにしている。</p>\n<p>関係ないが、舟ぜんというお店のマークがイケてた。</p>\n<blockquote class=\"instagram-media\" data-instgrm-version=\"6\" style=\" background:#FFF; border:0; border-radius:3px; box-shadow:0 0 1px 0 rgba(0,0,0,0.5),0 1px 10px 0 rgba(0,0,0,0.15); margin: 1px; max-width:658px; padding:0; width:99.375%; width:-webkit-calc(100% - 2px); width:calc(100% - 2px);\"><div style=\"padding:8px;\"> <div style=\" background:#F8F8F8; line-height:0; margin-top:40px; padding:50.0% 0; text-align:center; width:100%;\"> <div style=\" background:url(data:image/png;base64,); display:block; height:44px; margin:0 auto -44px; position:relative; top:-22px; width:44px;\"></div></div><p style=\" color:#c9c8cd; font-family:Arial,sans-serif; font-size:14px; line-height:17px; margin-bottom:0; margin-top:8px; overflow:hidden; padding:8px 0 7px; text-align:center; text-overflow:ellipsis; white-space:nowrap;\"><a href=\"https://www.instagram.com/p/BBbU2tjSxjR/\" style=\" color:#c9c8cd; font-family:Arial,sans-serif; font-size:14px; font-style:normal; font-weight:normal; line-height:17px; text-decoration:none;\" target=\"_blank\">Yuta Ideさん(@ide_yuta)が投稿した写真</a> - <time style=\" font-family:Arial,sans-serif; font-size:14px; line-height:17px;\" datetime=\"2016-02-06T01:49:22+00:00\">2016  2月 5 5:49午後 PST</time></p></div></blockquote><br />\n<p>江ノ島観光には飽きているので、参拝した足で江ノ電に乗り込む。街を縫って走る光景はいつ見ても楽しい。電車では寝がちな娘もはしゃいでいた。<br>\n特に行く当てを決めていたわけではなかったので、とりあえず長谷で降りる。長谷駅そばにあるどら焼きを食べる。うまい。</p>\n<blockquote class=\"instagram-media\" data-instgrm-version=\"6\" style=\" background:#FFF; border:0; border-radius:3px; box-shadow:0 0 1px 0 rgba(0,0,0,0.5),0 1px 10px 0 rgba(0,0,0,0.15); margin: 1px; max-width:658px; padding:0; width:99.375%; width:-webkit-calc(100% - 2px); width:calc(100% - 2px);\"><div style=\"padding:8px;\"> <div style=\" background:#F8F8F8; line-height:0; margin-top:40px; padding:50.0% 0; text-align:center; width:100%;\"> <div style=\" background:url(data:image/png;base64,); display:block; height:44px; margin:0 auto -44px; position:relative; top:-22px; width:44px;\"></div></div><p style=\" color:#c9c8cd; font-family:Arial,sans-serif; font-size:14px; line-height:17px; margin-bottom:0; margin-top:8px; overflow:hidden; padding:8px 0 7px; text-align:center; text-overflow:ellipsis; white-space:nowrap;\"><a href=\"https://www.instagram.com/p/BBbY33Fyxp5/\" style=\" color:#c9c8cd; font-family:Arial,sans-serif; font-size:14px; font-style:normal; font-weight:normal; line-height:17px; text-decoration:none;\" target=\"_blank\">Yuta Ideさん(@ide_yuta)が投稿した写真</a> - <time style=\" font-family:Arial,sans-serif; font-size:14px; line-height:17px;\" datetime=\"2016-02-06T02:24:28+00:00\">2016  2月 5 6:24午後 PST</time></p></div></blockquote><br />\n<p>良い時間なので娘の授乳のために授乳室のある施設を探す。親になると気づくことだが、意外と授乳室の整備されている観光地は少ない。長谷は長谷こども会館という施設で可能なようだ。お弁当も食べられる。長谷こども会館は明治41年に建てられた洋館を再利用していて、レトロ感に趣がある。</p>\n<p><a href=\"https://www.google.co.jp/search?q=%E9%95%B7%E8%B0%B7%E5%AD%90%E3%81%A9%E3%82%82%E4%BC%9A%E9%A4%A8&amp;espv=2&amp;biw=1352&amp;bih=668&amp;source=lnms&amp;tbm=isch&amp;sa=X&amp;ved=0ahUKEwi39Lnyj-PKAhXENJQKHR68DxQQ_AUIBygC\">長谷こども会館 | Google Image Search</a></p>\n<p>授乳とお昼を済ませて散歩する。おばあさんがタタミ一畳ほどの空間で営業しているパン屋さんで栗の入った食パンを買う。数ヶ月前から始めたパン作りも頑張ってお店のレベルに近づけたいものである。</p>\n<p>長谷駅へ向かいつつ散策する途中で、BELLOURIAという謎のお店を発見する。古い民家にしか見えない店構えなうえ、小さい看板が出ているだけでなんの店だか全くわからない。なぜだか妻が興味を示して中へ入っていく。オーダーメイドアクセサリーや自社で製造してる革製品、窯元から仕入れている手作りの食器類など、質の高い製品を集めたセレクトショップだった。その中にヌメ革の靴があり、オーロラシューズと呼ばれるなんでも界隈ではかなり有名な靴らしい。確かに歩きやすく良い品で、値段もそこまで高くない。数年前からヌメ革の靴が一足欲しいと思っていたが、なかなか良い品と出会えずにいた。そんなところでの出会いである。思い切って買ってしまった。妻も気に入ったらしく揃いで購入する。揃いで買うなんて実は初めてじゃないだろうか。</p>\n<blockquote class=\"instagram-media\" data-instgrm-version=\"6\" style=\" background:#FFF; border:0; border-radius:3px; box-shadow:0 0 1px 0 rgba(0,0,0,0.5),0 1px 10px 0 rgba(0,0,0,0.15); margin: 1px; max-width:658px; padding:0; width:99.375%; width:-webkit-calc(100% - 2px); width:calc(100% - 2px);\"><div style=\"padding:8px;\"> <div style=\" background:#F8F8F8; line-height:0; margin-top:40px; padding:50.0% 0; text-align:center; width:100%;\"> <div style=\" background:url(data:image/png;base64,); display:block; height:44px; margin:0 auto -44px; position:relative; top:-22px; width:44px;\"></div></div><p style=\" color:#c9c8cd; font-family:Arial,sans-serif; font-size:14px; line-height:17px; margin-bottom:0; margin-top:8px; overflow:hidden; padding:8px 0 7px; text-align:center; text-overflow:ellipsis; white-space:nowrap;\"><a href=\"https://www.instagram.com/p/BBcSGZyyxst/\" style=\" color:#c9c8cd; font-family:Arial,sans-serif; font-size:14px; font-style:normal; font-weight:normal; line-height:17px; text-decoration:none;\" target=\"_blank\">Yuta Ideさん(@ide_yuta)が投稿した写真</a> - <time style=\" font-family:Arial,sans-serif; font-size:14px; line-height:17px;\" datetime=\"2016-02-06T10:44:32+00:00\">2016  2月 6 2:44午前 PST</time></p></div></blockquote>\n<br />\n<p>15時を回っていたので江ノ電に乗り込み帰路につく。帰り道にオーロラシューズを検索してみると、通常オーダーから3ヶ月待ちがザラにある人気商品で、ニューヨークのオーロラ村で手作業で作られているらしい。良い出会いをした。</p>\n"}

