	module.exports = {"title":"PostCSS","date":"2016-03-18 14:22:25 (金)","layout":"post","path":"/20160318/","body":"<h2>PostCSS</h2>\n<p><a href=\"https://github.com/postcss/postcss\">https://github.com/postcss/postcss</a></p>\n<ul>\n<li>PostCSSはJavaScript製のCSSパーサ</li>\n<li>いろんなプラグインを組み合わせてモダンなCSS開発環境を作り出せる</li>\n<li>webpackと合わせて使うと便利</li>\n</ul>\n<h3>Webpack使用例</h3>\n<ol>\n<li>まずはインストール</li>\n</ol>\n<ul>\n<li>\n<p>PostCSS本体とLoaderに加えて、よく使うCSSNext、import、mixinsといったプラグインを入れる</p>\n</li>\n<li>\n<p>postcss-loaderに通すだけではファイルとして書き出されないので、file-loaderを使う</p>\n<pre><code><span class=\"hljs-built_in\">npm</span> i postcss postcss-loader file-loader postcss-cssnext postcss-<span class=\"hljs-keyword\">import</span> postcss-mixins\n</code></pre>\n</li>\n</ul>\n<ol start=\"2\">\n<li>\n<p>webpack.config.jsを作る</p>\n<pre><code class=\"language-js\"><span class=\"hljs-comment\">// PostCSSの記述のみで単純化した例</span>\n\n<span class=\"hljs-keyword\">var</span> cssnext = <span class=\"hljs-built_in\">require</span>(<span class=\"hljs-string\">'postcss-cssnext'</span>);\n<span class=\"hljs-keyword\">var</span> atImport = <span class=\"hljs-built_in\">require</span>(<span class=\"hljs-string\">'postcss-import'</span>);\n<span class=\"hljs-keyword\">var</span> atMixins = <span class=\"hljs-built_in\">require</span>(<span class=\"hljs-string\">'postcss-mixins'</span>);\n\n<span class=\"hljs-built_in\">module</span>.exports = {\n  <span class=\"hljs-attr\">context</span>: <span class=\"hljs-string\">`<span class=\"hljs-subst\">${__dirname}</span>/src`</span>,\n  <span class=\"hljs-attr\">entry</span>: {\n    <span class=\"hljs-attr\">css</span>: <span class=\"hljs-string\">'./styles/base.css'</span>\n  },\n  <span class=\"hljs-attr\">module</span>: {\n    <span class=\"hljs-attr\">loaders</span>: [{\n      <span class=\"hljs-attr\">loader</span>: <span class=\"hljs-string\">'file?name=[name].[ext]!postcss-loader'</span>,\n      <span class=\"hljs-attr\">test</span>: <span class=\"hljs-regexp\">/\\.css$/</span>\n    }]\n  },\n  <span class=\"hljs-attr\">output</span>: {\n    <span class=\"hljs-attr\">path</span>: <span class=\"hljs-string\">`<span class=\"hljs-subst\">${__dirname}</span>/dist`</span>\n  },\n  <span class=\"hljs-attr\">postcss</span>: <span class=\"hljs-function\"><span class=\"hljs-params\">()</span> =&gt;</span> [atImport, atMixins, cssnext]\n};\n</code></pre>\n</li>\n<li>\n<p>これでdistディレクトリにコンパイル済みのbase.cssが吐き出される</p>\n</li>\n</ol>\n<h3>便利プラグインたち</h3>\n<ul>\n<li><a href=\"https://github.com/postcss/postcss-import\">import</a>\n<ul>\n<li>@importが使えるようになる</li>\n<li>node_modules下のファイルもimportできる</li>\n</ul>\n</li>\n<li><a href=\"https://github.com/postcss/postcss-mixins\">mixins</a>\n<ul>\n<li>@mixinsが使えるようになる</li>\n</ul>\n</li>\n<li><a href=\"https://github.com/postcss/postcss-mixins\">conditionals</a>\n<ul>\n<li>@ifが使えるようになる</li>\n</ul>\n</li>\n<li><a href=\"https://github.com/postcss/autoprefixer\">autoprefixer</a>\n<ul>\n<li>vender-prefixをつけてくれる</li>\n</ul>\n</li>\n<li><a href=\"https://github.com/MoOx/postcss-cssnext\">cssnext</a>\n<ul>\n<li>策定中のCSS記法を使えるようにしてくれる</li>\n</ul>\n</li>\n</ul>\n"}

