	module.exports = {"title":"MediaQueryの分岐を分かりやすくしたい","date":"2016-03-28 15:24:13 (月)","layout":"post","path":"/20160328/","body":"<h1>MediaQueryの分岐を分かりやすくしたい in PostCSS</h1>\n<ul>\n<li>MediaQueryでスタイルを分岐させる場合にベタ書きすると見づらくなる</li>\n<li>PostCSSに限った話ではないけど</li>\n<li>mixinを「スタイルを展開する関数」のように使ってみる</li>\n<li>mixinなので他のファイルからも呼べてしまう問題\n<ul>\n<li>関数でも一緒じゃね</li>\n</ul>\n</li>\n</ul>\n<pre><code class=\"language-css\">@define-mixin Hoge { width: 100px; }\n@define-mixin Hoge-phone { width: 50px; }\n\n.Hoge {\n  @mixin Hoge;\n  @media (width &lt;= 480) { @mixin Hoge-phone; }\n}\n</code></pre>\n<p>=&gt;</p>\n<pre><code class=\"language-css\"><span class=\"hljs-selector-class\">.Hoge</span> { <span class=\"hljs-attribute\">width</span>: <span class=\"hljs-number\">100px</span>; }\n@<span class=\"hljs-keyword\">media</span> (max-width: <span class=\"hljs-number\">480px</span>) {\n  <span class=\"hljs-selector-class\">.Hoge</span> { <span class=\"hljs-attribute\">width</span>: <span class=\"hljs-number\">50px</span>; }\n}\n</code></pre>\n"}

