	module.exports = {"title":"ベイトソン","date":"2016-04-07 06:43:54 (木)","layout":"post","path":"/20160407/","body":"<h2>1241夜『デカルトからベイトソンへ』モリス・バーマン|松岡正剛の千夜千冊</h2>\n<p><a href=\"http://1000ya.isis.ne.jp/1241.html\">http://1000ya.isis.ne.jp/1241.html</a></p>\n<blockquote>\n<p>わかりやすくいうのなら、ベイトソンは、次のような世界観を提示した。〈〉の中がデカルト的な世界観である。</p>\n</blockquote>\n<blockquote>\n<ul>\n<li>① 事実と価値とは分けられない。〈事実は科学が積み上げる〉</li>\n<li>② 自然は人間との関係のなかで解釈される。〈自然は人間が外側から観察したものである〉</li>\n<li>③ 物質・精神（意識）・身体は大いなるプロセスの別々の側面であろう。〈物質と精神（意識）は別々のものである〉</li>\n<li>④ 理論はＡにもＢにも及んで進むべきである。〈理論はＡかＢかを選択しながら構築されるべきものである〉</li>\n<li>⑤ 科学は関係を記述するべきである。〈科学は物質の運動に関する記述を確立すべきである〉</li>\n<li>⑥ 全体は部分とは異なる特性をもつ。〈全体は部分の特性によって説明できる〉</li>\n</ul>\n</blockquote>\n<blockquote>\n<p>これはあえてデカルト的世界観と比較したベイトソン的世界観なので、かなりの誇張があるけれど、だいたいはこういうものだろう。</p>\n</blockquote>\n"}

