	module.exports = {"title":"デザイン","date":"2016-04-08 08:25:22 (金)","layout":"post","path":"/20160408/","body":"<h2>スペキュラティヴ・デザインが拓く思考 ──設計プロセスから未来投機的ヴィジョンへ</h2>\n<p><a href=\"http://10plus1.jp/monthly/2016/04/issue-01.php\">http://10plus1.jp/monthly/2016/04/issue-01.php</a></p>\n<blockquote>\n<p>「開発が終了した段階で、人間はどのように暮らしている可能性があるのか」「新たに開発される製品によって人間はどう変わる可能性があるのか」といった問題と、「実現するためにどのような技術を開発、実装すればよいのか」という問題がつねに並列に語られているのがインタラクション・デザインの領域の特徴のような気がしています</p>\n</blockquote>\n<blockquote>\n<p>スタートアップ界隈で求められているのは「圧倒的なヴィジョン」の構築だということです。今までの常識では考えられないものや、あるいは直接的にユーザーのニーズを反映したものではないプロダクトやサービスを実現するためには、魅力的でわかりやすいヴィジョンを構築し、それを提示していくことでエンジニアや投資家を集めなければなりません。ヴィジョンが訴求力になるわけです</p>\n</blockquote>\n<h2>インクルーシブデザインによるソーシャル・インタラクションの設計可能性についての素描</h2>\n<p><a href=\"http://www.daijirom.com/wp-content/uploads/%E3%82%A4%E3%83%B3%E3%82%AF%E3%83%AB%E3%83%BC%E3%82%B7%E3%83%95%E3%82%99%E3%83%86%E3%82%99%E3%82%B5%E3%82%99%E3%82%A4%E3%83%B3%E3%81%AB%E3%82%88%E3%82%8B%E3%82%BD%E3%83%BC%E3%82%B7%E3%83%A3%E3%83%AB%E3%83%BB%E3%82%A4%E3%83%B3%E3%82%BF%E3%83%A9%E3%82%AF%E3%82%B7%E3%83%A7%E3%83%B3%E3%81%AE%E8%A8%AD%E8%A8%88%E5%8F%AF%E8%83%BD%E6%80%A7%E3%81%AB%E3%81%A4%E3%81%84%E3%81%A6%E3%81%AE%E7%B4%A0%E6%8F%8F.pdf\">PDF - 2012</a></p>\n<blockquote>\n<p>インクルーシブデザインとは、これまで除外 ( エクスクルード:exclude) されてきた人々−−例えば障がい者や高齢者、外国人など、社会的にこれまでマイノリティとして除外されてきたかのような立場にある人々−−との恊働を通して差異を差異として包摂( インクルード:include)し、人々の尊厳を担保すると同時にビジネスとしても成り立つソーシャル・インタラクションをデザインすることである</p>\n</blockquote>\n<blockquote>\n<p>インクルーシブデザインの背景には、ヴィクター・パパネック『生きのびるためのデザイン』(1971) に代表される大量生産・大量消費されるデザインへの批判的態度と、発展途上国に住む人や障がいがある人らのためにデザインは何ができるか、という根源的な問いかけがある</p>\n</blockquote>\n<blockquote>\n<p>インクルーシブデザインの背景には、他者を理解し、相違を称賛し( 積極的な受容) 、モラルと責任を考慮した上で、「同化」ではなく、個人の相違を尊重し、他者との共存を目指した社会構築を目指す、というソーシャル・インクルージョンの理念がある</p>\n</blockquote>\n<blockquote>\n<p>ヘレンハムリンセンターのジュリア・カセムは「基本的には両概念は同一のものであるが、違いは、ユニバーサルデザインが、理念として成立しても実践は不可能に近いのに対し、インクルーシブデザインはできるだけ多くのグループを含めてデザインを実践していく現実的なアプローチである」としている</p>\n</blockquote>\n<blockquote>\n<p>インクルーシブデザインでは、「みんな同じ」という平等性を拙速に求めるのではなく、目の前にいるその人といかに対等に向き合うかというところからスタートする。</p>\n</blockquote>\n"}

